.parallax-container {
  position: relative;
  overflow: hidden;
}
.parallax-bg {
  position: absolute;
  top: -20%;
  left: 0;
  width: 100%;
  height: 140%;
  background-size: cover;
  background-position: center;
  will-change: transform;
}
.hero-slide {
  position: relative;
}
.hero-slide::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(0, 47, 87, 0.7) 0%,
    rgba(0, 47, 87, 0.5) 50%,
    rgba(0, 47, 87, 0.7) 100%
  );
